@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Lexend:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;1,400;1,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lexend:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;1,400;1,500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lexend:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;1,400;1,500&display=swap");
* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  display: block;
}

@font-face {
  font-family: "omega";
  src: url("../../src/fonts/Omega/BROmega-Medium.ttf") format("trueType");
  font-display: swap;
}
h1 {
  font-size: var(--font-size-h1);
}

h2 {
  font-size: var(--font-size-h2);
}

h3 {
  font-size: var(--font-size-h3);
}

h4 {
  font-size: var(--font-size-h4);
}

h5 {
  font-size: var(--font-size-h5);
}

h6 {
  font-size: var(--font-size-h6);
}

:root {
  --site-width-large: 84rem;
  --site-width-medium: 67rem;
  --site-width-small: 55rem;
  --site-width-mobile: 50rem;
}

.wrapper {
  --wrapper-max-width: var(--site-width-large);
  max-width: var(--wrapper-max-width);
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 92rem) {
  .wrapper {
    max-width: min(var(--site-width-medium), var(--wrapper-max-width));
  }
}
@media (max-width: 75rem) {
  .wrapper {
    max-width: min(var(--site-width-small), var(--wrapper-max-width));
  }
}
@media (max-width: 60rem) {
  .wrapper {
    max-width: min(var(--site-width-mobile), var(--wrapper-max-width));
  }
}
@media (max-width: 52rem) {
  .wrapper {
    max-width: calc(100% - 2rem);
  }
}

.nav-wrapper {
  max-width: calc(100% - 5rem);
}
@media (min-width: 92rem) {
  .nav-wrapper {
    max-width: var(--site-width-large);
  }
}
@media (max-width: 52rem) {
  .nav-wrapper {
    max-width: calc(100% - 2rem);
  }
}

body {
  width: 100%;
  height: var(--doc-height);
}

:root {
  --primary: #000;
  --secondary: #fff;
  --primary-bg: transparent;
  --secondary-bg: #f6f4f0;
  --section-border: #e7e7e7;
  --header-border: #f1f1f1;
  --footer-text: #b8b8b8;
  --social-bg: #000;
  --social-icon: #fefefe;
  --about-subtitle: #484642;
  --about-link: #c7c4c2;
  --related-resource: #202020;
  --related-resource-border: #c0c0c0;
  --image-cover: rgba(0, 0, 0, 0.5);
  --paragraph-text: #999999;
  --link: #979797;
  --link-border: #505050;
  --featured-paragraph: #969696;
  --other-stories-bio: #6a6a6a;
  --sub-title: #979594;
  --bio: #83817e;
  --read-link: #343130;
  --max-width: 1152px;
  --small: 400;
  --middle: 500;
  --semi-bold: 600;
  --bold: 700;
  --lexend: "Lexend", sans-serif;
  --lora: "Lora", serif;
  --font-size-h1: 2.75em;
  --font-size-h2: 2.5em;
  --font-size-h3: 2.25em;
  --font-size-h4: 2em;
  --font-size-h5: 1.875em;
  --font-size-h6: 1.75em;
}

.button {
  font-size: 0.75em;
  align-self: center;
  padding: 0.9375rem 1.25rem;
  border: 1px solid var(--primary);
  color: var(--primary);
  background-color: transparent;
}
.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
}
.button:hover {
  background-color: var(--secondary-bg);
}
.button__large {
  width: 12em;
  font-size: 1.25em;
  font-weight: var(--semi-bold);
  margin-top: 1.875rem;
}
.button__small {
  color: var(--related-resource);
  text-transform: uppercase;
}

.section {
  background-color: var(--primary-bg);
  color: var(--primary);
}
.section__dark {
  background-color: var(--secondary-bg);
}

.introduction-section {
  width: 100%;
  margin-top: 10em;
}
@media only screen and (max-width: 990px) {
  .introduction-section {
    margin-top: 8em;
  }
}
.introduction-section .introduction-container {
  margin: 10rem auto 3.125rem auto;
  display: grid;
  grid-template-columns: 0.7fr 1fr;
  grid-template-rows: unset;
  gap: 1.25rem;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  font-family: var(--lexend);
}
@media only screen and (max-width: 990px) {
  .introduction-section .introduction-container {
    margin: 7rem auto;
  }
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container {
    grid-template-columns: unset;
    grid-template-rows: 0.6fr 1.5fr;
    gap: 2.5rem;
    margin: 1.25rem auto 0rem auto;
  }
}
.introduction-section .introduction-container.active .introduction-text-container {
  transform: translateX(0rem);
  opacity: 1;
}
.introduction-section .introduction-container.active .introduction-picture-section {
  transform: translateY(0rem);
  opacity: 1;
}
.introduction-section .introduction-container .introduction-text-container {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  padding: 0.625rem;
}
@media only screen and (min-width: 1150px) {
  .introduction-section .introduction-container .introduction-text-container {
    opacity: 0;
    transform: translateX(-9.375em);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
  }
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-text-container {
    justify-content: flex-start;
    height: unset;
  }
}
.introduction-section .introduction-container .introduction-text-container .introduction-subtitle {
  text-transform: uppercase;
  font-size: 0.75em;
  color: #585858;
  font-weight: var(--middle);
}
.introduction-section .introduction-container .introduction-text-container .introduction-title {
  font-family: omega;
  font-weight: var(--middle);
}
.introduction-section .introduction-container .introduction-text-container .introduction-paragraph {
  font-size: 0.875em;
  line-height: 1.6;
  color: var(--link);
}
.introduction-section .introduction-container .introduction-text-container .introduction-link {
  border-bottom: 1px solid var(--link-border);
}
.introduction-section .introduction-container .introduction-text-container .introduction-link span {
  display: inline-block;
  position: relative;
  font-size: 0.75em;
  text-transform: uppercase;
  cursor: pointer;
}
.introduction-section .introduction-container .introduction-picture-section {
  display: grid;
  grid-template-columns: 1.3fr 0.6fr 0.8fr;
  grid-template-rows: 0.5fr 3fr;
  gap: 0px;
  justify-content: unset;
  align-items: unset;
  height: 100%;
}
@media only screen and (min-width: 1150px) {
  .introduction-section .introduction-container .introduction-picture-section {
    opacity: 0;
    transform: translateY(9.375em);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-photo-container {
  grid-row: 1/3;
  grid-column: 1/3;
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-photo-container {
    grid-column: 1/4;
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-photo-container img {
  height: 34em;
  margin-bottom: -6.25rem;
}
@media only screen and (min-width: 1400px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-photo-container img {
    height: 45em;
  }
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-photo-container img {
    margin-bottom: 0px;
    max-width: 100%;
    height: unset;
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container {
  grid-row: 2/3;
  grid-column: 2/4;
  padding: 1.875rem 1.25rem;
  border: 5px solid var(--primary);
  display: flex;
  flex-direction: column;
  gap: 2.1875rem;
  justify-content: space-between;
  align-items: center;
  font-family: var(--lora);
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container {
    background-color: var(--image-cover);
    color: var(--secondary);
    grid-row: 1/4;
    grid-column: 1/4;
    justify-content: space-between;
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-title {
  text-transform: uppercase;
  font-size: 0.75em;
  font-style: italic;
  font-weight: var(--middle);
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-quoted-text {
  text-align: center;
  font-size: 0.875em;
  line-height: 1.3;
  font-weight: var(--middle);
  color: #3e3e3e;
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-quoted-text {
    font-size: 1.125em;
    color: var(--secondary);
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-quoted-text q {
  quotes: "“" "”" "‘" "’";
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-quoted-text q::before {
  content: open-quote;
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .introduction-picture-quoted-text q::after {
  content: close-quote;
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container {
  display: flex;
  flex-direction: column;
  gap: 5px;
  justify-content: center;
  align-items: center;
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container .hidden-onion-logo {
  width: 1.375rem;
  height: 1.375rem;
  margin-bottom: 8px;
}
@media only screen and (max-width: 800px) {
  .introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container .hidden-onion-logo {
    width: 1.875rem;
    height: 1.875rem;
  }
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container .company-logo-slogan-1, .introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container .company-logo-slogan-2 {
  text-transform: uppercase;
  font-size: 0.75em;
  font-weight: var(--small);
}
.introduction-section .introduction-container .introduction-picture-section .introduction-picture-text-container .company-logo-container .company-logo-slogan-2 {
  font-weight: var(--semi-bold);
}

.representation-section {
  width: 100%;
  margin: 0 auto;
}
.representation-section .representation-container {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  justify-content: center;
  align-items: center;
  font-family: var(--lora);
  padding: 9.375rem 0rem 5rem 0rem;
}
@media only screen and (max-width: 800px) {
  .representation-section .representation-container {
    padding: 3.125rem 0rem;
    align-items: flex-start;
  }
}
.representation-section .representation-container.active .representation-section-title {
  opacity: 1;
  transform: translateY(0rem);
}
.representation-section .representation-container.active .representation-section-subtitle {
  opacity: 1;
  transform: translateY(0rem);
}
.representation-section .representation-container.active .representatives-grid {
  opacity: 1;
  transform: translateY(0rem);
}
.representation-section .representation-container .representation-section-title {
  font-weight: var(--small);
}
@media only screen and (min-width: 1150px) {
  .representation-section .representation-container .representation-section-title {
    transform: translateY(3.125rem);
    opacity: 0;
    transition: opacity 1s ease-out, transform 1s ease-out;
  }
}
.representation-section .representation-container .representation-section-subtitle {
  flex-basis: 50%;
  margin: 0 auto;
  font-weight: var(--semi-bold);
  font-size: 0.875em;
  color: var(--sub-title);
  line-height: 1.5;
  text-align: center;
  margin-bottom: 2.5rem;
}
@media only screen and (min-width: 1150px) {
  .representation-section .representation-container .representation-section-subtitle {
    opacity: 0;
    transition: opacity 1s ease-out, transform 1s ease-out;
    transform: translateY(3.125rem);
  }
}
@media only screen and (max-width: 800px) {
  .representation-section .representation-container .representation-section-subtitle {
    max-width: 90%;
    margin: unset;
    text-align: start;
  }
}
.representation-section .representation-container .representatives-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  grid-template-rows: unset;
  gap: 1.875rem;
  justify-content: center;
  align-items: flex-start;
  max-width: 100%;
}
@media only screen and (min-width: 1150px) {
  .representation-section .representation-container .representatives-grid {
    transform: translateY(18.75rem);
    opacity: 0;
    transition: opacity 2s ease-out, transform 2s ease-out;
  }
}
@media only screen and (min-width: 1400px) {
  .representation-section .representation-container .representatives-grid {
    transition: opacity 1s ease-out, transform 1s ease-out;
  }
}
.representation-section .representation-container .representatives-grid .representative-card {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  justify-content: flex-start;
  align-items: center;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-image-container {
  position: relative;
  width: 100%;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-image-container .representative-image {
  width: 100%;
  min-height: 21.25rem;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-image-container .representative-image-label {
  position: absolute;
  bottom: -1px;
  left: 0px;
  padding: 12px 12px 7px 12px;
  background-color: var(--secondary-bg);
  display: flex;
  flex-direction: row;
  gap: 0.625rem;
  justify-content: center;
  align-items: flex-start;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-image-container .representative-image-label .representative-logo {
  width: 1.25rem;
  height: 1.25rem;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-image-container .representative-image-label span {
  font-style: italic;
  font-weight: var(--small);
  font-size: 0.875em;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-information {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
  justify-content: flex-start;
  align-items: flex-start;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-information .representative-name {
  letter-spacing: 1px;
  font-size: 1.125em;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-information .representative-position {
  font-weight: var(--small);
  font-size: 0.875em;
  margin-bottom: 0.625rem;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-information .representative-bio {
  max-width: 90%;
  color: var(--bio);
  font-weight: var(--middle);
  font-size: 0.875em;
  margin-bottom: 0.625rem;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-information .read-feature-link {
  border-bottom: 1px solid var(--read-link);
  transition: opacity 0.2s ease-out, transform 0.2s ease-out;
}
.representation-section .representation-container .representatives-grid .representative-card .representative-information .read-feature-link span {
  display: inline-block;
  position: relative;
  color: var(--read-link);
  font-size: 0.75em;
  font-weight: var(--middle);
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
}

.featured-section {
  width: 100%;
}
.featured-section .featured-container {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  gap: 3.75rem;
  justify-content: center;
  align-items: flex-start;
  font-family: var(--lora);
  padding: 3.125em 0rem;
  border-bottom: 2px solid var(--section-border);
}
@media only screen and (max-width: 800px) {
  .featured-section .featured-container {
    flex-direction: column;
    gap: 1.875rem;
  }
}
.featured-section .featured-container.active .featured-story {
  opacity: 1;
  transform: translateX(0px);
}
.featured-section .featured-container.active .other-story-container {
  opacity: 1;
  transform: translateY(0px);
}
.featured-section .featured-container .featured-story {
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  gap: 1.875rem;
  justify-content: flex-start;
  align-items: flex-start;
}
@media only screen and (min-width: 1150px) {
  .featured-section .featured-container .featured-story {
    opacity: 0;
    transform: translateX(-9.375em);
    transition: opacity 2s ease-out, transform 2s ease-out;
  }
}
.featured-section .featured-container .featured-story .featured-image-container {
  width: 100%;
}
.featured-section .featured-container .featured-story .featured-image-container .featured-image {
  width: 100%;
}
.featured-section .featured-container .featured-story .featured-content {
  display: flex;
  flex-direction: column;
  gap: 1.875rem;
  justify-content: flex-start;
  align-items: flex-start;
}
.featured-section .featured-container .featured-story .featured-content .featured-content-category {
  display: flex;
  flex-direction: row;
  gap: 0.625rem;
  justify-content: flex-start;
  align-items: center;
}
.featured-section .featured-container .featured-story .featured-content .featured-content-category .featured-logo {
  width: 1.5625rem;
  height: 1.5625rem;
}
.featured-section .featured-container .featured-story .featured-content .featured-content-category .category {
  font-style: italic;
  font-size: 1.25em;
}
.featured-section .featured-container .featured-story .featured-content .featured-title {
  font-weight: var(--middle);
}
.featured-section .featured-container .featured-story .featured-content .subtitle {
  text-transform: uppercase;
  font-size: 0.75em;
  font-weight: var(--bold);
  letter-spacing: 1px;
}
.featured-section .featured-container .featured-story .featured-content .featured-paragraph {
  font-size: 1em;
  color: var(--featured-paragraph);
  font-weight: var(--semi-bold);
  max-width: 90%;
}
.featured-section .featured-container .other-story-container {
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  gap: 1.875rem;
  justify-content: flex-start;
  align-items: flex-start;
  padding-top: 1.25em;
  font-family: var(--lexend);
}
@media only screen and (min-width: 1150px) {
  .featured-section .featured-container .other-story-container {
    opacity: 0;
    transform: translateY(9.375em);
    transition: opacity 2s ease-out, transform 2s ease-out;
  }
}
.featured-section .featured-container .other-story-container .other-story-title {
  font-weight: var(--bold);
  text-transform: capitalize;
}
.featured-section .featured-container .other-story-container .stories-container {
  display: grid;
  grid-template-columns: unset;
  grid-template-rows: unset;
  gap: 1.875rem;
  justify-content: flex-start;
  align-items: flex-start;
}
@media only screen and (max-width: 990px) {
  .featured-section .featured-container .other-story-container .stories-container {
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    align-items: center;
  }
}
@media only screen and (max-width: 560px) {
  .featured-section .featured-container .other-story-container .stories-container {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }
}
.featured-section .featured-container .other-story-container .stories-container .story-content {
  display: flex;
  flex-direction: row;
  gap: 1.875rem;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}
.featured-section .featured-container .other-story-container .stories-container .story-content .story-number {
  font-size: 3em;
  font-weight: var(--small);
}
.featured-section .featured-container .other-story-container .stories-container .story-content .story-info {
  display: flex;
  flex-direction: column;
  gap: 7px;
  justify-content: flex-start;
  align-items: flex-start;
}
.featured-section .featured-container .other-story-container .stories-container .story-content .story-info .story-title {
  font-weight: var(--semi-bold);
  font-size: 1em;
}
.featured-section .featured-container .other-story-container .stories-container .story-content .story-info .story-bio {
  font-size: 0.875em;
  font-weight: var(--middle);
  color: var(--other-stories-bio);
}

.the-latest-section {
  width: 100%;
}
.the-latest-section .the-latest-container {
  padding: 5rem 0rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  justify-content: flex-start;
  align-items: flex-start;
  border-bottom: 2px solid var(--section-border);
}
.the-latest-section .the-latest-container.active .the-latest-title {
  opacity: 1;
  transform: translateX(0rem);
}
.the-latest-section .the-latest-container.active .the-latest-grid {
  opacity: 1;
}
.the-latest-section .the-latest-container .the-latest-title {
  text-transform: capitalize;
  font-weight: var(--middle);
}
@media only screen and (min-width: 1150px) {
  .the-latest-section .the-latest-container .the-latest-title {
    opacity: 0;
    transform: translateX(-9.375rem);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
