@font-face {
  font-family: "omega";
  src: url("../../src/fonts/Omega/BROmega-Medium.ttf") format("trueType");
  font-display: swap;
}
h1 {
  font-size: var(--font-size-h1);
}

h2 {
  font-size: var(--font-size-h2);
}

h3 {
  font-size: var(--font-size-h3);
}

h4 {
  font-size: var(--font-size-h4);
}

h5 {
  font-size: var(--font-size-h5);
}

h6 {
  font-size: var(--font-size-h6);
}

:root {
  --site-width-large: 84rem;
  --site-width-medium: 67rem;
  --site-width-small: 55rem;
  --site-width-mobile: 50rem;
}

.wrapper {
  --wrapper-max-width: var(--site-width-large);
  max-width: var(--wrapper-max-width);
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 92rem) {
  .wrapper {
    max-width: min(var(--site-width-medium), var(--wrapper-max-width));
  }
}
@media (max-width: 75rem) {
  .wrapper {
    max-width: min(var(--site-width-small), var(--wrapper-max-width));
  }
}
@media (max-width: 60rem) {
  .wrapper {
    max-width: min(var(--site-width-mobile), var(--wrapper-max-width));
  }
}
@media (max-width: 52rem) {
  .wrapper {
    max-width: calc(100% - 2rem);
  }
}

.nav-wrapper {
  max-width: calc(100% - 5rem);
}
@media (min-width: 92rem) {
  .nav-wrapper {
    max-width: var(--site-width-large);
  }
}
@media (max-width: 52rem) {
  .nav-wrapper {
    max-width: calc(100% - 2rem);
  }
}

body {
  width: 100%;
  height: var(--doc-height);
}

:root {
  --primary: #000;
  --secondary: #fff;
  --primary-bg: transparent;
  --secondary-bg: #f6f4f0;
  --section-border: #e7e7e7;
  --header-border: #f1f1f1;
  --footer-text: #b8b8b8;
  --social-bg: #000;
  --social-icon: #fefefe;
  --about-subtitle: #484642;
  --about-link: #c7c4c2;
  --related-resource: #202020;
  --related-resource-border: #c0c0c0;
  --image-cover: rgba(0, 0, 0, 0.5);
  --paragraph-text: #999999;
  --link: #979797;
  --link-border: #505050;
  --featured-paragraph: #969696;
  --other-stories-bio: #6a6a6a;
  --sub-title: #979594;
  --bio: #83817e;
  --read-link: #343130;
  --max-width: 1152px;
  --small: 400;
  --middle: 500;
  --semi-bold: 600;
  --bold: 700;
  --lexend: "Lexend", sans-serif;
  --lora: "Lora", serif;
  --font-size-h1: 2.75em;
  --font-size-h2: 2.5em;
  --font-size-h3: 2.25em;
  --font-size-h4: 2em;
  --font-size-h5: 1.875em;
  --font-size-h6: 1.75em;
}

.button {
  font-size: 0.75em;
  align-self: center;
  padding: 0.9375rem 1.25rem;
  border: 1px solid var(--primary);
  color: var(--primary);
  background-color: transparent;
}
.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
}
.button:hover {
  background-color: var(--secondary-bg);
}
.button__large {
  width: 12em;
  font-size: 1.25em;
  font-weight: var(--semi-bold);
  margin-top: 1.875rem;
}
.button__small {
  color: var(--related-resource);
  text-transform: uppercase;
}

.section {
  background-color: var(--primary-bg);
  color: var(--primary);
}
.section__dark {
  background-color: var(--secondary-bg);
}